/* App */

#root {
  background: #000;
  width: 100%;
  height: 100%;
}

/* Home */

.home {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* Card */

.card {
  width: 420px;
  height: 100%;
  background: #fff;
  padding: 16px;
  border-radius: 5px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  display: flex;
  flex-direction: column;
}

/* Profile Pic */

.profile-pic {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-pic img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.profile-name {
  font-size: 2.4rem;
  text-align: center;
  font-weight: 500;
  letter-spacing: 0.0125em;
  margin-top: 12px;
}

/* Bio */

.bio {
  margin-top: 6px;
  font-size: 1.6rem;
  color: rgba(0, 0, 0, 0.6);
  text-align: center;
}

/* Contact */

.contact {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
/* Links List */

.links-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.links-list-title {
  padding-top: 12px;
  font-size: 2rem;
  font-weight: 500;
}

.links-list-link {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.6);
  letter-spacing: 0.0125em;
  font-size: 1.6rem;
  padding-top: 2px;
}

.links-list-link:hover {
  text-decoration: underline;
}

/* Gallery */

.gallery {
  position: absolute;
  left: 0;
  right: 0;
  height: auto;
  background: #fff;
  border-radius: 5px;
  width: 800px;
  margin: 0 auto;
}

.gallery-title {
  font-size: 2.4rem;
  text-align: center;
  font-weight: 500;
  letter-spacing: 0.0125em;
  margin-top: 12px;
}

.gallery-image-container {
  padding: 0 30px 0;
  height: 100%;
}

.gallery-image {
  margin-top: 12px;
  margin-bottom: 20px;
  border: 1px solid #000;
  border-radius: 2px;
}

.gallery-home-button {
  padding-top: 12px;
  position: fixed;
  font-size: 2.4rem;
  font-weight: 500;
  letter-spacing: 0.0125em;
  top: 0;
  right: 10px;
  text-decoration: none;
  color: #fff;
  transition: 0.4s;
}

.gallery-home-button:hover {
  color: #000;
}
